.card-back {
  height: 400px;
  width: 260px;
  cursor: pointer;
  background: #CCC5B3;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
  position: absolute;
  overflow: hidden;
  border: solid 1px #696969;
  border-radius: 6px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}

.card-back:before {
  content: "";
  position: absolute;
  background: #0C0B13;
  -webkit-transform: rotate(72deg) translate(75px, 20px);
          transform: rotate(72deg) translate(75px, 20px);
  top: 20px;
  bottom: 0;
  left: 0;
  right: -30px;
  border-left: solid 111px #664C4F;
}

.card-back:after {
  content: "";
  position: absolute;
  background: rgba(225, 98, 63, 0.7);
  -webkit-transform: rotate(58deg) translate(0px, 0px);
          transform: rotate(58deg) translate(0px, 0px);
  top: -50px;
  bottom: -50px;
  left: 62px;
  right: 60px;
}

.card-back__content {
  letter-spacing: -1px;
  z-index: 10;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.card-back__title {
  font-size: 22pt;
  line-height: 29px;
  color: #0C0B13;
  font-family: 'Josefin-Sans';
  margin: 0;
}

.card-back__subtitle {
  font-size: 22pt;
  line-height: 29px;
  font-family: 'Josefin-Sans';
  margin: 0;
}

.card-back__title-mark {
  font-size: 15pt;
  top: -0.3em;
  color: #0C0B13;
}

.card {
  cursor: pointer;
  background: #CCC5B3;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
  border: solid 1px #696969;
  border-radius: 6px;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding: 0;
  margin: 0;
  font-family: 'Josefin-Sans';
  word-wrap: normal;
}

.card__mark {
  position: absolute;
  font-size: 0.25em;
  color: #ffffff;
  top: 5px;
  left: 5px;
}

.card__mark-upside-down {
  position: absolute;
  font-size: 0.25em;
  color: #E1623F;
  bottom: 5px;
  right: 5px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.card__points {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.cards {
  padding: 5px 0 0 0;
  width: 100%;
}

.cards__card {
  margin: 5px 3% 5px 3%;
  display: inline-block;
  height: 90px;
  width: 19%;
  font-size: 31pt;
}

.lobby {
  font-family: 'Josefin-Sans';
  position: absolute;
  width: 100%;
  z-index: 1;
  bottom: 20px;
  right: 80px;
}

.lobby__player {
  position: relative;
  float: right;
  height: 40px;
  width: 19%;
  margin: 5px 3% 5px 3%;
  background: #CCC5B3;
  border: solid 1px #696969;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
}

.lobby__player-content {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.lobby__player-name {
  font-size: 25pt;
}

.message {
  margin-bottom: 5px;
  border: 1px solid transparent;
  padding: 5px;
  text-align: center;
  font-size: 14px;
  font-family: "-apple-system","Helvetica Neue","Roboto","Segoe UI",sans-serif;
  line-height: 20px;
}

.message:empty {
  display: none;
}

.message--state-success {
  color: #3c763d;
  background-color: #dff0d8;
  border-color: #d6e9c6;
}

.message--state-error {
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1;
}

.message--state-warning {
  color: #8a6d3b;
  background-color: #fcf8e3;
  border-color: #faebcc;
}

.players__player {
  float: right;
  clear: both;
  height: 60px;
  width: 19%;
  cursor: pointer;
  background: #CCC5B3;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
  border: solid 1px transparent;
  border-radius: 6px;
  margin: 5px 3% 5px 3%;
}

.players__player--points-different {
  border-color: #ef473a;
}

.players__player--points-equal {
  border-color: #33cd5f;
}

.players__player-content {
  position: relative;
  font-size: 31pt;
  color: #0C0B13;
  text-align: center;
}

.players__player-mark {
  font-size: 17pt;
  color: #ffffff;
  position: relative;
  font-weight: bold;
  margin: 0 0 0 5px;
  height: 15px;
}

.players {
  font-family: 'Josefin-Sans';
  position: absolute;
  width: 100%;
  z-index: 1;
  bottom: 6px;
}

.about-list {
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  color: #444;
  margin-top: 56px;
  text-align: center;
  border: none;
  font-size: 14px;
  min-height: 48px;
}

.about-list__item {
  text-align: center;
  border: none;
  font-size: 14px;
  min-height: 48px;
  padding: 16px;
}

.about__logo-image {
  border: 1px solid rgba(0, 0, 0, 0.25);
  padding: 3px;
  border-radius: 50%;
  box-sizing: content-box;
  margin: 0 auto;
  width: 96px;
  height: 96px;
}

.about__product-name {
  padding-top: 20px;
  font-size: 23px;
  font-weight: bold;
  line-height: 1.2;
}

.about__product-version {
  margin: 0 0 2px;
  font-size: 16px;
  font-weight: 400;
}

.about__product-version p {
  margin: 0 0 10px;
  color: #666;
  font-size: 14px;
}

.about__form-button,
.about__form-button:hover,
.about__form-button:focus {
  text-decoration: none;
  color: #444;
  border: none;
  background: transparent;
}

.about__form-icon {
  font-size: 22pt;
}

.about__footer {
  bottom: 16px;
  left: 16px;
  right: 16px;
  position: absolute;
  text-align: center;
}

.menu > div {
  background-color: #424242;
}

.menu svg,
.menu span {
  color: #fff !important;
}

.menu__header {
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  font-size: 14px;
  background-color: #78909c;
  min-height: 150px;
  color: #fff;
  position: relative;
}

.menu__button-back {
  float: right;
  display: -webkit-flex;
  display: flex;
  min-height: 56px;
  -webkit-align-items: center;
          align-items: center;
}

.menu__logo-image {
  border: 1px solid rgba(0, 0, 0, 0.25);
  padding: 1px;
  border-radius: 50%;
  box-sizing: content-box;
  top: 16px;
  left: 16px;
  height: 65px;
  width: 65px;
  position: absolute;
  background-color: #fff;
}

.menu__title {
  bottom: 16px;
  left: 16px;
  position: absolute;
  font-size: 1.3em
}

.home {
